import React, { Component, Suspense } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import Card from "../Elements/Card";

class SwiperFalling extends Component {
    constructor(props) {
        super(props);
        this.myRef = React.createRef();
    }

    componentWillReceiveProps(nextProps) {
        const { activeID, pIndex } = nextProps;
        if (activeID == pIndex && this.myRef.current && this.props.activeID !== activeID) {
            this.myRef.current.swiper.slideNext();
        }
    }
    onUpdate = () => {
        const { pIndex } = this.props;
        setTimeout(() => {
            window.dispatchEvent(new Event("resize"));
            setTimeout(() => {
                const wrap = document.getElementById(`tb_wall_list_wrap___${pIndex + 1}`);
                if (wrap) wrap.classList.add("tb_fc_animation");
            }, 250);
        }, 100);
    };


    render() {
        const { pIndex, itemIds, activeID, completeDataObject, wall, onClickToCTA, postSizeInPx, cardCount, clickToShowPopUp } = this.props

        return (
            <Swiper
                ref={this.myRef}
                autoHeight={true}
                slidesPerView={"auto"}
                direction="vertical"
                spaceBetween={0}
                mousewheel={true}
                loop={true}
                speed={2000}
                allowTouchMove={false}
                autoplay={{ enabled: false, disableOnInteraction: true }}
                id={`tb_wall_list_wrap___${pIndex + 1}`}
                className="swiper-container tb_wall_list_wrap___"
                key={`tb_wall_list_wrap___${pIndex + 1}`}
                style={{ width: `${100 / cardCount}%` }}
                onAfterInit={this.onUpdate}
            >
                {itemIds?.length ? itemIds.map((item, index) => {
                    const cardData = completeDataObject[item];
                    return <SwiperSlide key={`item_${index}_${pIndex}`}>
                        <Card isVisible={true} ownerId={wall.Wall.owner} itemData={cardData} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} wall={wall} />
                    </SwiperSlide>
                }) : null}
            </Swiper>
        );
    }
}

export default SwiperFalling;